.container {
  position: relative;
  background-color: #fff;
  width: 70%;
  margin: auto;
  padding: 3rem 0;
  z-index: 2;
  border: 2px white solid;
  border-radius: 3px;

}

.img_container {
    position: absolute;
    height: 90vh;
    width: 100vw;

}

.img_container img {

  width: 100vw;
  height: 90vh;
  object-fit:cover;
  opacity: .9;
  -webkit-filter: contrast(120%) saturate(125%) brightness(104%);
          filter: contrast(120%) saturate(125%) brightness(104%);
  background-image: linear-gradient(rgba(255,255,255,1), rgb(47,47,47));
}


.img_container::after {
  display: block;
  position: relative;
  background-image: linear-gradient(to bottom, transparent 0%, rgb(27,31,60) 100%);
  margin-top: -35rem;
  height: 35rem;
  width: 100%;
  content: '';
  z-index: 0;
}

