.loading_container{
  padding:  7em 0;
  height: 20rem;
  width: 70%;
}

.loading_box {
  width:50px;
  height:50px;
  position: relative;
  -webkit-animation: loading 5s ease-in-out 1s infinite ; /* Safari 4.0 - 8.0 */
  animation: loading 5s ease-in-out 1s infinite ;
}

@-webkit-keyframes loading {
  0%  {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353; left:50%; top:0px;}
  9% {-webkit-transform: scale(1);transform: scale(1); background-color:#ffc965; left:50%; top:0px;}
  18%  {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353; left:50%; top:0px;}

  27% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965; left:60%; top:0px;}
  36% {-webkit-transform: scale(1);transform: scale(1); background-color:#cca353; left:60%; top:0px;}
  45% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965; left:60%; top:0px;}

  54% {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353;  left:70%; top:0px;}
  63% {-webkit-transform: scale(1);transform: scale(1); background-color:#ffc965; left:70%; top:0px;}
  72% {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353;  left:70%; top:0px;}

  81% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965;  left:80%; top:0px;}
  90% {-webkit-transform: scale(1);transform: scale(1); background-color:#cca353; left:80%; top:0px;}
  100% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965;  left:80%; top:0px;}
}

@keyframes loading {
  0%  {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353; left:50%; top:0px;}
  9% {-webkit-transform: scale(1);transform: scale(1); background-color:#ffc965; left:50%; top:0px;}
  18%  {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353; left:50%; top:0px;}

  27% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965; left:60%; top:0px;}
  36% {-webkit-transform: scale(1);transform: scale(1); background-color:#cca353; left:60%; top:0px;}
  45% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965; left:60%; top:0px;}

  54% {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353;  left:70%; top:0px;}
  63% {-webkit-transform: scale(1);transform: scale(1); background-color:#ffc965; left:70%; top:0px;}
  72% {-webkit-transform: scale(0);transform: scale(0); background-color:#cca353;  left:70%; top:0px;}

  81% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965;  left:80%; top:0px;}
  90% {-webkit-transform: scale(1);transform: scale(1); background-color:#cca353; left:80%; top:0px;}
  100% {-webkit-transform: scale(0);transform: scale(0); background-color:#ffc965;  left:80%; top:0px;}
}

.container {
  width: 80%;
}

.featured__cards {
  width: 100%;
  margin: auto;
}

.card__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  padding: 2rem 0;
  width: 100%;
}


.card {
  display: grid;
  grid-template-rows: 2fr 1fr;
  border: 2px solid #e7e7e7;
  border-radius: 5px;
  margin: 0;
}

.card__img {
  height: 12rem;
  width: 100%;
  overflow: hidden;
  border-radius: 5px;

}

.card__img img {
  width: 100%;
  height: 12rem;
  object-fit: cover;
  /* margin-left: -1rem; */
}


.card_info {

  line-height: 1.6;
  overflow: hidden;
  width: 100%;
  padding: 7px;
}


.card_info_cat{
  font-size: .8rem;
  width: 100%;
}

.card__header {
  color: #2f2f2f;
  font-size: .9rem;
  font-weight: bold;
  overflow: hidden;
}

.card_info__rating {
  font-size: .8rem;
}

